import {
	Grid,
	List,
	ListItem,
	ListItemText,
	Typography,
} from '@material-ui/core';
import _isEmpty from 'lodash.isempty';
import React from 'react';
import _get from 'lodash.get';
import { PropertyData } from '../../../../../types';

type Props = {
	propertyData: PropertyData;
};

const DataTreeInformation = ({ propertyData }: Props) => {
	if (_isEmpty(propertyData)) return <></>;
	return (
		<>
			<Grid item>
				<List disablePadding>
					<ListItem>
						<ListItemText primary='Owners' />
						<Typography variant='body2'>
							{_get(propertyData, 'OwnerNames')}
						</Typography>
					</ListItem>
				</List>
			</Grid>
			<Grid item>
				<List disablePadding>
					<ListItem>
						<ListItemText primary='Property Address' />
						<Typography variant='body2'>
							{_get(propertyData, 'FullAddress')}
						</Typography>
					</ListItem>
				</List>
			</Grid>
		</>
	);
};

export default DataTreeInformation;
