import {
	Grid,
	List,
	ListItem,
	ListItemText,
	Typography,
} from '@material-ui/core';
import _isEmpty from 'lodash.isempty';
import _get from 'lodash.get';
import React from 'react';
import { TReportStatus } from '../../../../../types';

type Props = {
	taxReportStatus: TReportStatus;
};

const TaxReport = ({ taxReportStatus }: Props) => {
	if (_isEmpty(taxReportStatus)) return <></>;
	return (
		<>
			<Grid item>
				<List disablePadding>
					<ListItem>
						<ListItemText primary='Tax Resource Status' />
						<Typography variant='body2'>
							{_get(taxReportStatus, 'TaxReportTaxResourceStatus')}
						</Typography>
					</ListItem>
				</List>
			</Grid>
			<Grid item>
				<List disablePadding>
					<ListItem>
						<ListItemText primary='Tax Resource Message' />
						<Typography variant='body2'>
							{_get(taxReportStatus, 'TaxReportTaxResourceMessage')}
						</Typography>
					</ListItem>
				</List>
			</Grid>
			<Grid item>
				<List disablePadding>
					<ListItem>
						<ListItemText primary='Total View Status' />
						<Typography variant='body2'>
							{_get(taxReportStatus, 'TaxReportTotalViewStatus')}
						</Typography>
					</ListItem>
				</List>
			</Grid>
			<Grid item>
				<List disablePadding>
					<ListItem>
						<ListItemText primary='Total View Message' />
						<Typography variant='body2'>
							{_get(taxReportStatus, 'TaxReportTotalViewMessage')}
						</Typography>
					</ListItem>
				</List>
			</Grid>
		</>
	);
};

export default TaxReport;
