import React from 'react';
import { FormikHelpers, useFormik } from 'formik';
import { Button, Grid, Stack, TextField } from '@material-ui/core';
import { gql, useMutation } from '@apollo/client';
import _get from 'lodash.get';
import { AddSellerSchema } from './Validation';
import { TAddProperty } from '../../../../../types';
import { useSnackBar } from '../../../../../hooks';
import InputLabel from '../../../../../ui-component/extended/Form/InputLabel';
import { PhoneInput } from '../../../components/PhoneInput';
import config from '../../../../../config';
import AnimateButton from '../../../../../ui-component/extended/AnimateButton';
import graphqlSchema from '../../../../../graphql';

type Props = {
	handleNext: () => void;
	formData: any;
	refetch: any;
};

const AddSellerInformation = (props: Props) => {
	const { handleNext, formData, refetch } = props;
	const sellerInfo = _get(formData, 'SellerInfo');
	const { Email: email, FirstName: firstName, LastName: lastName } = sellerInfo;
	const propertyId = _get(formData, 'PropertyId');

	const phone = _get(sellerInfo, 'Phone', '') || '';

	const [updateSellerInformation] = useMutation<TAddProperty>(
		gql(graphqlSchema.updateSellerInformation)
	);
	const showSnackBar = useSnackBar();
	const onSubmitHandler = async (
		values: TAddProperty,
		{ setStatus, setSubmitting }: FormikHelpers<TAddProperty>
	) => {
		try {
			const { email, firstName, lastName, phone } = values;
			const variables = {
				PropertyId: propertyId,
				SellerInfo: {
					Email: email,
					FirstName: firstName,
					LastName: lastName,
					Phone: phone || undefined,
				},
			};
			updateSellerInformation({
				variables,
			});
			showSnackBar({
				message: 'Account has updated',
				alertSeverity: 'success',
			});
			handleNext();
			refetch();
		} catch (e: any) {
			const message = _get(e, 'message', 'Something went wrong');
			setStatus({ success: false });
			setSubmitting(false);
			showSnackBar({ message });
		}
	};

	const formik = useFormik({
		initialValues: {
			email,
			firstName,
			lastName,
			phone,
		},
		validationSchema: AddSellerSchema,
		onSubmit: onSubmitHandler,
	});
	const {
		touched,
		errors,
		handleSubmit,
		values,
		handleChange,
		handleBlur,
		isSubmitting,
		setFieldValue,
	} = formik;
	return (
		<form noValidate onSubmit={handleSubmit}>
			<Grid container spacing={3}>
				<Grid item xs={12} lg={6}>
					<InputLabel>First Name</InputLabel>
					<TextField
						defaultValue={values.firstName}
						fullWidth
						placeholder='First Name'
						onChange={handleChange}
						onBlur={handleBlur}
						error={touched.firstName && Boolean(errors.firstName)}
						helperText={touched.firstName && errors.firstName}
						name='firstName'
					/>
				</Grid>
				<Grid item xs={12} lg={6}>
					<InputLabel>Last Name</InputLabel>
					<TextField
						defaultValue={values.lastName}
						fullWidth
						placeholder='Last Name'
						onChange={handleChange}
						onBlur={handleBlur}
						error={touched.lastName && Boolean(errors.lastName)}
						helperText={touched.lastName && errors.lastName}
						name='lastName'
					/>
				</Grid>
				<Grid item xs={12} lg={6}>
					<InputLabel>Email</InputLabel>
					<TextField
						defaultValue={values.email}
						fullWidth
						placeholder='Email'
						onChange={handleChange}
						onBlur={handleBlur}
						error={touched.email && Boolean(errors.email)}
						helperText={touched.email && errors.email}
						name='email'
					/>
				</Grid>
				<Grid item xs={12} lg={6}>
					<InputLabel>Phone</InputLabel>
					<PhoneInput
						country={config.locate.countryCode}
						placeholder=''
						value={values.phone}
						onChange={(value: string) => setFieldValue('phone', value)}
						onBlur={handleBlur}
						name='phone'
						error={touched.phone && errors.phone}
					/>
				</Grid>
				<Grid item xs={12}>
					<Stack direction='row' justifyContent='flex-end'>
						<AnimateButton>
							<Button
								color='primary'
								variant='contained'
								type='submit'
								disabled={isSubmitting}
							>
								Next
							</Button>
						</AnimateButton>
					</Stack>
				</Grid>
			</Grid>
		</form>
	);
};
export default AddSellerInformation;
