import { useFormikContext } from 'formik';
import {
	Grid,
	List,
	ListItem,
	ListItemText,
	TextField,
	Typography,
} from '@material-ui/core';
import React from 'react';
import _get from 'lodash.get';
import {
	TPropertyListingInformation,
	TSellRequest,
} from '../../../../../types';

type Props = {
	property: TSellRequest;
	isDisableYearBuilt: boolean;
};

const AddYearBuilt = ({ property, isDisableYearBuilt }: Props) => {
	const { values, handleChange, touched, errors, handleBlur } =
		useFormikContext<TPropertyListingInformation>();

	return (
		<>
			<Grid item>
				<List disablePadding>
					<ListItem>
						<ListItemText primary='Original Year Built' />
						<Typography variant='body2'>
							{_get(property, ['PropertyData', 'YearBuilt'])}
						</Typography>
					</ListItem>
				</List>
			</Grid>
			{!isDisableYearBuilt && (
				<List disablePadding>
					<ListItem>
						<ListItemText primary='Add Year Built' />
						<Typography variant='body2'>
							<TextField
								id='yearBuilt'
								value={values.yearBuilt}
								fullWidth
								placeholder='Year Built'
								onChange={handleChange}
								onBlur={handleBlur}
								error={touched.yearBuilt && Boolean(errors.yearBuilt)}
								helperText={touched.yearBuilt && errors.yearBuilt}
								name='yearBuilt'
								disabled={isDisableYearBuilt}
							/>
						</Typography>
					</ListItem>
				</List>
			)}
		</>
	);
};

export default AddYearBuilt;
