import { useFormikContext } from 'formik';
import {
	Grid,
	List,
	ListItem,
	ListItemText,
	TextField,
	Typography,
} from '@material-ui/core';
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import { MaterialUiPickersDate } from '@material-ui/pickers/typings/date';
import React from 'react';
import DecimalInput from '../../../components/DecimalInput';
import { TEditProperty } from '../../../../../types';
import { TERMINATION_DATE_FORMAT } from '../../config';

const AddListingFee = () => {
	const { values, handleChange, handleBlur, touched, errors, setFieldValue } =
		useFormikContext<TEditProperty>();

	return (
		<>
			<Grid item>
				<List disablePadding>
					<ListItem>
						<ListItemText primary='Buyer Agent Fee (%)' />
						<Typography variant='body2'>
							<TextField
								id='buyerAgentFeePct'
								value={values.buyerAgentFeePct}
								fullWidth
								placeholder='Buyer Agent Fee (%)'
								onChange={handleChange}
								onBlur={handleBlur}
								error={touched.buyerAgentFeePct && Boolean(errors.buyerAgentFeePct)}
								helperText={touched.buyerAgentFeePct && errors.buyerAgentFeePct}
								name='buyerAgentFeePct'
								InputProps={{
									inputComponent: DecimalInput as any,
								}}
							/>
						</Typography>
					</ListItem>
				</List>
			</Grid>
			<Grid item>
				<List disablePadding>
					<ListItem>
						<ListItemText primary='Listing Agent Fee (%)' />
						<Typography variant='body2'>
							<TextField
								id='listingAgentFeePct'
								value={values.listingAgentFeePct}
								fullWidth
								placeholder='Listing Agent Fee (%)'
								onChange={handleChange}
								onBlur={handleBlur}
								error={touched.listingAgentFeePct && Boolean(errors.listingAgentFeePct)}
								helperText={touched.listingAgentFeePct && errors.listingAgentFeePct}
								name='listingAgentFeePct'
								InputProps={{
									inputComponent: DecimalInput as any,
								}}
							/>
						</Typography>
					</ListItem>
				</List>
			</Grid>
			<Grid item>
				<List disablePadding>
					<ListItem>
						<ListItemText primary='Listing Termination Date' />
						<Typography variant='body2'>
							<MuiPickersUtilsProvider utils={DateFnsUtils}>
								<DatePicker
									disablePast
									format={TERMINATION_DATE_FORMAT}
									value={new Date(values.listingTerminationDate)}
									fullWidth
									placeholder='Listing Termination Date'
									onChange={(date: MaterialUiPickersDate) =>
										setFieldValue('listingTerminationDate', date)
									}
									name='listingTerminationDate'
								/>
							</MuiPickersUtilsProvider>
						</Typography>
					</ListItem>
				</List>
			</Grid>
		</>
	);
};

export default AddListingFee;
