import React, { useEffect } from 'react';
import { Step, Stepper, StepLabel } from '@material-ui/core';
import { gql, useMutation } from '@apollo/client';
import _get from 'lodash.get';
import { useParams, useHistory, generatePath } from 'react-router-dom';
import MainCard from '../../../../../ui-component/cards/MainCard';
import {
	STEPS,
	ADD_SELLER_INFORMATION_STEP,
	ADD_PROPERTY_STEP,
	ADD_DATA_TREE_STEP,
} from './config';
import AddSellerInformation from './AddSellerInformation';
import AddDataTree from './AddDataTree';
import AddProperty from './AddProperty';
import { TSellRequest } from '../../../../../types';
import graphqlSchema from '../../../../../graphql';
import { PATHS } from '../../../../../routes/path';

type Props = {
	formData: TSellRequest;
	startPolling: any;
	stopPolling: any;
	vestingTypesData: Array<string>;
	refetch: any;
};

const getStepContent = (
	step: number,
	handleNext: () => void,
	handleBack: () => void,
	formData: TSellRequest,
	startPolling: any,
	stopPolling: any,
	vestingTypesData: Array<string>,
	refetch: any
) => {
	switch (step) {
		case ADD_SELLER_INFORMATION_STEP:
			return (
				<AddSellerInformation
					refetch={refetch}
					handleNext={handleNext}
					formData={formData}
				/>
			);
		case ADD_DATA_TREE_STEP:
			return (
				<AddDataTree
					handleNext={handleNext}
					handleBack={handleBack}
					formData={formData}
					startPolling={startPolling}
					stopPolling={stopPolling}
					refetch={refetch}
				/>
			);
		case ADD_PROPERTY_STEP:
			return (
				<AddProperty
					handleBack={handleBack}
					formData={formData}
					vestingTypesData={vestingTypesData}
				/>
			);
		default:
			throw new Error('Unknown step');
	}
};

const Wizard = ({
	formData,
	startPolling,
	stopPolling,
	vestingTypesData,
	refetch,
}: Props) => {
	const history = useHistory();
	const { step } = useParams<{ step: string }>();
	const [activeStep, setActiveStep] = React.useState(0);
	const { PropertyId: propertyId } = formData;
	const currentStep = +(step || _get(formData, 'NewPropertyWizardStep'));
	const [updatePropertyStep] = useMutation(
		gql(graphqlSchema.updatePropertyStep)
	);
	const addHistory = (step: number) => {
		const link = generatePath(PATHS.PROPERTY_WIZARD, {
			id: propertyId,
			step,
		});
		history.push(link);
	};
	const handleNext = () => {
		const step = activeStep + 1;
		setActiveStep(step);
		updatePropertyStep({
			variables: {
				PropertyId: propertyId,
				NewPropertyWizardStep: step,
			},
		});
		addHistory(step);
	};

	const handleBack = () => {
		const step = activeStep - 1;
		setActiveStep(step);
		updatePropertyStep({
			variables: {
				PropertyId: propertyId,
				NewPropertyWizardStep: step,
			},
		});
		addHistory(step);
	};

	useEffect(() => {
		setActiveStep(currentStep);
	}, [currentStep]);

	return (
		<MainCard title='Property'>
			<Stepper activeStep={activeStep} sx={{ pt: 3, pb: 5 }}>
				{STEPS.map(label => {
					const labelProps = {};
					return (
						<Step key={label}>
							<StepLabel {...labelProps}>{label}</StepLabel>
						</Step>
					);
				})}
			</Stepper>
			<>
				{getStepContent(
					activeStep,
					handleNext,
					handleBack,
					formData,
					startPolling,
					stopPolling,
					vestingTypesData,
					refetch
				)}
			</>
		</MainCard>
	);
};

export default Wizard;
