import React, { useEffect } from 'react';
import { FormikHelpers, useFormik } from 'formik';
import _get from 'lodash.get';
import { gql, useMutation } from '@apollo/client';
import {
	Button,
	CircularProgress,
	FormHelperText,
	Grid,
	Stack,
	TextField,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import {
	TSellRequest,
	TDataTreeProperty,
	PropertyData,
} from '../../../../../types';
import { selectDataTreeProperty } from '../../../../../graphql/mutations';
import { AddDataTreeSchema } from './Validation';
import { useSnackBar } from '../../../../../hooks';
import SearchProperty from './SearchProperty';
import AnimateButton from '../../../../../ui-component/extended/AnimateButton';
import TaxReport from './TaxReport';
import { shouldStopPolling, isReady } from '../../Helper';
import DataTreeInformation from './DataTreeInformation';

const useStyles = makeStyles({
	hidden: {
		display: 'none',
	},
});

type Props = {
	handleNext: () => void;
	handleBack: () => void;
	formData: TSellRequest;
	startPolling: any;
	stopPolling: any;
	refetch: any;
};

const PULLING_SECOND = 7000;

const AddDataTree = (props: Props) => {
	const {
		handleNext,
		formData,
		handleBack,
		startPolling,
		stopPolling,
		refetch,
	} = props;

	const { DTPropertyId: dataTreePropertyId } = formData;
	const showSnackBar = useSnackBar();
	const classes = useStyles();

	const [selectDataTreePropertyAction, { loading: isSaving }] = useMutation(
		gql(selectDataTreeProperty)
	);
	const {
		PropertyId: id,
		ReportStatus: taxReportStatus,
		PropertyData: propertyData,
	} = formData;

	const isStop = shouldStopPolling(formData);

	const onSelectProperty = async (property: PropertyData) => {
		try {
			const { PropertyId } = property;
			await selectDataTreePropertyAction({
				variables: {
					PropertyId: id,
					DataTreePropertyId: PropertyId,
				},
			});
			refetch();
			startPolling(PULLING_SECOND);
		} catch (e) {
			const message = _get(e, 'message', 'Something went wrong');
			showSnackBar({ message });
			stopPolling();
		}
	};

	const onSubmitHandler = async (
		values: TDataTreeProperty,
		{ setStatus, setSubmitting }: FormikHelpers<TDataTreeProperty>
	) => {
		try {
			stopPolling();
			handleNext();
		} catch (e: any) {
			const message = _get(e, 'message', 'Something went wrong');
			setStatus({ success: false });
			setSubmitting(false);
			showSnackBar({ message });
		}
	};

	const isDisabled = !isReady(formData);

	useEffect(() => {
		if (isStop) stopPolling();
	}, [isStop]);

	useEffect(() => {
		if (isDisabled && dataTreePropertyId) startPolling(PULLING_SECOND);
		if (!isDisabled) stopPolling();
	}, [isDisabled]);

	const formik = useFormik({
		initialValues: {
			dataTreePropertyId,
		},
		validationSchema: AddDataTreeSchema,
		onSubmit: onSubmitHandler,
	});

	const { handleSubmit, touched, errors, values } = formik;

	return (
		<form noValidate onSubmit={handleSubmit}>
			<Grid container spacing={3}>
				<Grid item xs={12} lg={12}>
					<SearchProperty onSelect={onSelectProperty} />
					<TextField
						type='hidden'
						name='dataTreePropertyId'
						value={values.dataTreePropertyId || ''}
						id='dataTreePropertyId'
						className={classes.hidden}
					/>
					{touched.dataTreePropertyId && errors.dataTreePropertyId && (
						<FormHelperText error>Please search and select a property</FormHelperText>
					)}
				</Grid>
				<Grid item lg={12} xs={12}>
					<DataTreeInformation propertyData={propertyData} />
					<TaxReport taxReportStatus={taxReportStatus} />
				</Grid>
				<Grid item xs={12}>
					<Stack direction='row' justifyContent='flex-end'>
						<AnimateButton>
							<Button onClick={handleBack} sx={{ my: 3, ml: 1 }}>
								Back
							</Button>
							<Button
								color='primary'
								variant='contained'
								type='submit'
								disabled={isDisabled}
							>
								{isSaving ? (
									<CircularProgress size='1.5rem' color='secondary' />
								) : (
									'Next'
								)}
							</Button>
						</AnimateButton>
					</Stack>
				</Grid>
			</Grid>
		</form>
	);
};

export default AddDataTree;
