import { Formik } from 'formik';
import {
	Button,
	CircularProgress,
	Divider,
	Grid,
	Stack,
	makeStyles,
} from '@material-ui/core';
import _isEmpty from 'lodash.isempty';
import _get from 'lodash.get';
import { gql, useMutation } from '@apollo/client';
import React, { useEffect, useMemo } from 'react';
import { generatePath, useHistory } from 'react-router-dom';
import {
	TPropertyListingInformation,
	TSellRequest,
} from '../../../../../types';
import { addPreviewProp, convertToFile } from '../../../../../helpers';
import AnimateButton from '../../../../../ui-component/extended/AnimateButton';
import { isApproved } from '../../Rules';
import { useSnackBar, useUploadFiles } from '../../../../../hooks';
import { getPropertyThumbnailPutUrl } from '../../../../../graphql/queries';
import { AddPropertySchema } from './Validation';
import graphqlSchema from '../../../../../graphql';
import { updatePropertyCache } from '../../../../../dataProvider/cache';
import { gridSpacing } from '../../../../../store/constant';
import DataTreeInformation from './DataTreeInformation';
import AddVestingType from './AddVestingType';
import AddYearBuilt from './AddYearBuilt';
import AddListingPrice from './AddListingPrice';
import AddListingFee from './AddListingFee';
import AddOther from './AddOther';
import UploadFile from '../../../components/UploadFile';
import SubCard from '../../../../../ui-component/cards/SubCard';
import { PATHS } from '../../../../../routes/path';

const useStyles = makeStyles({
	button: {
		marginRight: '10px',
	},
});
type Props = {
	handleBack: () => void;
	formData: TSellRequest;
	vestingTypesData: Array<string>;
};

const AddListInformation = (props: Props) => {
	const classes = useStyles();
	const showSnackBar = useSnackBar();
	const history = useHistory();
	const { formData, handleBack, vestingTypesData } = props;
	const { files, onFilesChange, uploadFiles, isUploadLoading } = useUploadFiles({
		name: 'getPropertyThumbnailPutUrl',
		query: getPropertyThumbnailPutUrl,
		generateVariables: files => ({ Photos: files, PropertyId: propertyId }),
	});
	const [approveAction] = useMutation(gql(graphqlSchema.approveProperty));

	const {
		PropertyId: propertyId,
		PropertyData: propertyData,
		StatusId: statusId,
		BuyerAgentFeePct: buyerAgentFeePct,
		ListingAgentFeePct: listingAgentFeePct,
		ListingTerminationDate: listingTerminationDate = '',
		AdditionalInfo: additionalInfo = '',
		ListingTermsConditions: listingTermsConditions = 'No',
	} = formData;

	const vestingTypeMapped = _get(propertyData, 'VestingTypeMapped', '');

	const listPriceRange = _get(formData, 'ListPriceRange', {});
	const priceMax = _get(listPriceRange, 'Max', 0);
	const priceMin = _get(listPriceRange, 'Min', 0);

	const yearBuilt = String(
		_get(propertyData, 'YearBuilt', '') ||
			_get(propertyData, 'YearBuiltOverride', '') ||
			''
	);
	const vestingTypeTmp =
		vestingTypeMapped || _get(propertyData, 'VestingTypeOverride', '');
	const vestingType = String(
		vestingTypesData.includes(`${vestingTypeTmp}`) ? vestingTypeTmp : 'Unknown'
	);

	const approved = isApproved(statusId);
	const isDisableYearBuilt =
		approved || !_isEmpty(_get(propertyData, 'YearBuilt', ''));
	const isDisableVestingType = approved || !_isEmpty(vestingTypeMapped);

	useEffect(() => {
		const propertyThumbnail = _get(formData, 'Thumbnail', null);
		if (propertyThumbnail) onFilesChange([convertToFile(propertyThumbnail)]);
	}, [formData]);

	const [updatePropertyDataAction, { loading: isSaving }] = useMutation(
		gql(
			graphqlSchema.updatePropertyData({
				hasYearBuilt: !isDisableYearBuilt,
				hasVestingType: !isDisableVestingType,
			})
		),
		{
			update: updatePropertyCache,
			onCompleted: () => {
				showSnackBar({ message: 'Property updated', alertSeverity: 'success' });
			},
			onError: e => {
				const message = _get(e, 'message', 'Something went wrong');
				showSnackBar({ message });
			},
		}
	);

	const onSaveGeneralInformation = async (
		values: TPropertyListingInformation
	) => {
		const {
			vestingType,
			yearBuilt,
			priceMax,
			priceMin,
			buyerAgentFeePct,
			listingAgentFeePct,
			listingTerminationDate,
			additionalInfo,
			listingTermsConditions,
		} = values;
		uploadFiles();
		const variables = {
			ListingTermsConditions: listingTermsConditions,
			AdditionalInfo: additionalInfo,
			PropertyId: propertyId,
			...(!isDisableVestingType && {
				VestingType: { VestingType: vestingType },
			}),
			...(!isDisableYearBuilt && { YearBuilt: { YearBuilt: yearBuilt } }),
			ListPriceRange: { Max: priceMax, Min: priceMin },
			ListingAgentFeePct: listingAgentFeePct,
			BuyerAgentFeePct: buyerAgentFeePct,
			ListingTerminationDate: new Date(listingTerminationDate)
				.toISOString()
				.slice(0, 10),
		};
		await updatePropertyDataAction({
			variables,
		});
	};
	const onSave = (values: TPropertyListingInformation) =>
		onSaveGeneralInformation(values);

	const onSubmit = async (values: TPropertyListingInformation) => {
		await onSaveGeneralInformation(values);
		await approveAction({
			variables: {
				PropertyId: propertyId,
			},
		});
		const propertyLink = generatePath(PATHS.PROPERTY, {
			id: propertyId,
		});
		history.push(propertyLink);
	};

	const initialValues = useMemo(
		() => ({
			vestingType,
			priceMin,
			priceMax,
			yearBuilt,
			buyerAgentFeePct,
			listingAgentFeePct,
			listingTerminationDate: listingTerminationDate || new Date(),
			additionalInfo,
			listingTermsConditions,
		}),
		[]
	);

	return (
		<Grid container spacing={gridSpacing}>
			<Grid item xs={12}>
				<Formik
					initialValues={initialValues}
					onSubmit={onSubmit}
					validationSchema={AddPropertySchema}
				>
					{({ values, handleSubmit }) => (
						<form noValidate onSubmit={handleSubmit}>
							<Grid container spacing={3}>
								<Grid item xs={12} lg={12}>
									<DataTreeInformation propertyData={propertyData} />
								</Grid>
								<Grid item lg={12} xs={12}>
									<AddVestingType
										property={formData}
										vestingTypesData={vestingTypesData}
										isDisableVestingType={isDisableVestingType}
									/>
								</Grid>
							</Grid>
							<Divider />
							<Grid container spacing={3}>
								<Grid item xs={12} lg={12}>
									<AddYearBuilt
										property={formData}
										isDisableYearBuilt={isDisableYearBuilt}
									/>
								</Grid>
							</Grid>
							<Divider />
							<Grid container spacing={3}>
								<Grid item xs={12} lg={12}>
									<AddListingPrice />
								</Grid>
								<Grid item lg={12} xs={12}>
									<AddListingFee />
								</Grid>
							</Grid>
							<Grid container spacing={3}>
								<Grid item xs={12} lg={12}>
									<SubCard title='Property Photo'>
										<UploadFile
											name='thumbnail'
											files={files}
											maxFiles={1}
											onDrop={files => onFilesChange(addPreviewProp(files))}
											loading={isUploadLoading}
										/>
									</SubCard>
								</Grid>
							</Grid>
							<Grid container spacing={3}>
								<Grid item xs={12} lg={12}>
									<AddOther />
								</Grid>
							</Grid>
							<Grid container spacing={3}>
								<Grid item xs={12}>
									<Stack direction='row' justifyContent='flex-end'>
										<AnimateButton>
											<Button onClick={handleBack} sx={{ my: 3, ml: 1 }}>
												Back
											</Button>
											<Button
												className={classes.button}
												color='primary'
												variant='contained'
												type='button'
												disabled={isUploadLoading || isSaving}
												sx={{ my: 3, ml: 1 }}
												onClick={() => onSave(values)}
											>
												{isUploadLoading || isSaving ? (
													<CircularProgress size='1.5rem' color='secondary' />
												) : (
													'Save'
												)}
											</Button>
											<Button
												color='primary'
												variant='contained'
												type='submit'
												disabled={isUploadLoading || isSaving}
											>
												{isUploadLoading || isSaving ? (
													<CircularProgress size='1.5rem' color='secondary' />
												) : (
													'Approve'
												)}
											</Button>
										</AnimateButton>
									</Stack>
								</Grid>
							</Grid>
						</form>
					)}
				</Formik>
			</Grid>
		</Grid>
	);
};

export default AddListInformation;
