import { useFormikContext } from 'formik';
import {
	Select,
	MenuItem,
	Grid,
	List,
	ListItem,
	ListItemText,
	Typography,
} from '@material-ui/core';
import React from 'react';
import _get from 'lodash.get';
import {
	TPropertyListingInformation,
	TSellRequest,
} from '../../../../../types';

type Props = {
	property: TSellRequest;
	vestingTypesData: Array<string>;
	isDisableVestingType: boolean;
};

const AddVestingType = ({
	property,
	vestingTypesData,
	isDisableVestingType,
}: Props) => {
	const { values, handleChange } =
		useFormikContext<TPropertyListingInformation>();

	return (
		<>
			<Grid item>
				<List disablePadding>
					<ListItem>
						<ListItemText primary='Original Vesting Type' />
						<Typography variant='body2'>
							{_get(property, ['PropertyData', 'VestingOwnershipRight'], '-')}
						</Typography>
					</ListItem>
				</List>
			</Grid>
			<Grid item>
				<List disablePadding>
					<ListItem>
						<ListItemText primary='Mapped Vesting Type' />
						<Typography variant='body2'>
							{_get(property, ['PropertyData', 'VestingTypeMapped'], '-')}
						</Typography>
					</ListItem>
				</List>
			</Grid>
			{!isDisableVestingType && (
				<Grid item>
					<List disablePadding>
						<ListItem>
							<ListItemText primary='Vesting Type' />
							<Select
								defaultValue='Unknown'
								labelId='vesting-select'
								id='vestingType'
								name='vestingType'
								value={values.vestingType}
								onChange={handleChange}
								disabled={isDisableVestingType}
							>
								<MenuItem value='Unknown'>
									<em>Unknown</em>
								</MenuItem>
								{vestingTypesData.map((type: string) => (
									<MenuItem key={type} value={type}>
										{type}
									</MenuItem>
								))}
							</Select>
						</ListItem>
					</List>
				</Grid>
			)}
		</>
	);
};

export default AddVestingType;
