import React from 'react';
import { useParams } from 'react-router-dom';
import _get from 'lodash.get';
import { gql, useQuery } from '@apollo/client';
import { Grid } from '@material-ui/core';
import graphqlSchema from '../../../graphql';
import Wizard from '../../application/property/components/Wizard';
import Loader from '../../../ui-component/Loader';
import { gridSpacing } from '../../../store/constant';

const WizardPage = () => {
	const { id } = useParams<{ id: string }>();
	const { data, startPolling, stopPolling, loading, refetch } = useQuery(
		gql(graphqlSchema.getProperty),
		{
			variables: { PropertyId: id },
		}
	);
	const formData = _get(data, 'getProperty') || {};

	const { loading: isLoadingVestingTypes, data: vestingTypesData } = useQuery(
		gql(graphqlSchema.listSupportedVestingTypes)
	);
	const vestingTypes = _get(vestingTypesData, 'listSupportedVestingTypes');

	if (loading || isLoadingVestingTypes) return <Loader />;

	return (
		<Grid container spacing={gridSpacing} justifyContent='center'>
			<Grid item xs={12} md={9} lg={7}>
				<Wizard
					formData={formData}
					startPolling={startPolling}
					stopPolling={stopPolling}
					vestingTypesData={vestingTypes}
					refetch={refetch}
				/>
			</Grid>
		</Grid>
	);
};

export default WizardPage;
